<template>
  <div class="container">    
    <div class="main clearfix" :style="{'margin-left':$store.state.app.isCollapse ? '84px':'240px'}">
      <div class="main_container">
        <querys :showSwitch = "false">
          <div slot="queryTabs">
            <div class="query_tab">
              <div class="query_label">项目名称</div>
              <div class="query_select">
                <el-input v-model="param.itemName" @change ="selectItem()" size="small" placeholder="请输入内容"></el-input>
              </div>
            </div>

            <div class="query_tab">
              <div class="query_label">所属单位</div>
              <div class="query_select">
                <select-tree @getValue="getValue($event)"></select-tree>
              </div>
            </div>

            <div class="query_tab">
              <div class="query_label">专业类别</div>
              <div class="query_select">
                <el-select  clearable  placeholder="请选择" size="small" @change="selectItem()" v-model="param.speciality">
                   <el-option v-for="item in selectParamDict.get('speciality')" :key="item.codeValue" :label="item.codeName"
                                               :value="item.codeValue"></el-option>
              </el-select>
              </div>
            </div>

            <div class="query_tab">
              <div class="query_label">批次名称</div>
              <div class="query_select">
                <el-input type="text" v-model="param.batchName" 
                @change="selectItem()" 
                clearable size="small" 
                placeholder="请输入"></el-input>
              </div>
            </div>

            <div class="query_tab">
              <div class="query_label">状态</div>
              <div class="query_select">
                <el-select clearable  placeholder="请选择" size="small" @change="selectItem()" v-model="param.joinStatus">
                  <el-option v-for="item in statusList" :key="item.id" :label="item.name"
                                               :value="item.id"></el-option>
                </el-select>
              </div>
            </div>
          </div>
        </querys>
        <div class="table-item-box">
          <div :class="showDrawer?'table-wrapper-run':'table-wrapper-init'">
            <el-table 
              :data="tableShowData"
              border
              :header-cell-style="{background:'#359691',color:'#fff'}"
              row-class-name="row"
              cell-class-name="column"
              v-loading="tableDataLoding"
              :height="tableHeight"
              lazy
              :load="tableLoad"
              row-key="itemId"
              :tree-props="{children: 'children', hasChildren: 'hasChildren'}"
              >
              <el-table-column
                      type="selection"
                      width="45">
              </el-table-column>
              <el-table-column
                      type="index"
                      width="50"
                      label="序号">
              </el-table-column>
              <el-table-column
                      v-for="(item, index) in tableLabel"
                      v-if ="item.colDname != 'itemName'&&item.showType==1"
                      :key="index"
                      :prop="item.colDname"
                      :label="item.dispName"
                      min-width='220'
                      :show-overflow-tooltip="true">
              </el-table-column>
              <el-table-column v-else-if="item.showType==3"
                                :prop="item.colDname"
                              :label="item.dispName"
                              min-width='220'
                              :show-overflow-tooltip="true">
              </el-table-column>
              <el-table-column v-else-if="item.colDname == 'itemName'"
                              :label="item.dispName"
                              min-width='220'
                              :show-overflow-tooltip="true">
                <template slot-scope="scope" >
                  <span v-if="scope.row.isBatchPackage ==1" >{{ scope.row.itemName }}</span>
                  <span class="blue" v-else @click="showItemInfo(scope.row)">{{scope.row.itemName}}</span>
                </template>
              </el-table-column>
              <el-table-column label="状态" min-width='150'>
                <template slot-scope="scope">
                  <span >{{scope.row.auditStatus}}</span>
                </template>
              </el-table-column>
              <el-table-column label="操作"  min-width='100'>
                <template slot-scope="scope" v-if="scope.row.isEdit != false">
                  <span  class="blue" @click="showHasSelectedExpertDialog(scope.row)">查看</span>
                </template>
              </el-table-column>
            </el-table>
            
            <div class="page">
              <el-pagination
                background
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="currentPage"
                :page-sizes="[10,20,30,50]"
                :page-size="20"
                layout="sizes, total, prev, pager, next, jumper"
                :total="totalNum">
              </el-pagination>
            </div>
          </div>
          <div :class="showDrawer?'item-add-wrapper-run':'item-wrapper-init'">
						<!-- 新增 修改 浏览 引itemaddcom -->
						<item-add-com 
						v-if="showDrawer" 
						:key="timer" 
						:showData="showData" 
						:showAddDialog="showDrawer" 
            :isShowQuery="showQuery"
            :tableHeightTab="tableHeight"
						@changeStatus="changeStatus">
						</item-add-com>
					</div>
        </div>

        <!--查看已选专家列表-->
        <el-dialog :visible.sync="showHasSelectedExpert" :close-on-click-modal="false" class="diy_dialog"
        :modal-append-to-body='false' >
          <template slot="title">
            <div style="font-size: large"><span>查看专家</span></div>
          </template>
                  <el-table 
              :data="hasSelectedExpertInfoShow"
              class="dialog_table"
              max-height="480px" 
              border
              :header-cell-style="{background:'#359691',color:'#fff'}"
              row-class-name="row"
              cell-class-name="column"
              :highlight-current-row="true"
              >
              <el-table-column
                label="专家姓名"
                prop="expertName"
                >
              </el-table-column>
              <el-table-column
                label="专家性别"
                prop="expertSex"
                >
              </el-table-column>
              <el-table-column
                label="专业"
                prop="professional"
                >
              </el-table-column>
              <el-table-column
                label="评审职位"
                prop="isLeader"
                >
              </el-table-column>
              <el-table-column
                label="是否参加"
                prop="isJoin"
                >
              </el-table-column>
              <el-table-column
                label="参加意见"
                prop="notJoinReason"
                >
              </el-table-column>
              <el-table-column
                label="审核意见"
                prop="isAudit"
                >
              </el-table-column>
              <el-table-column
                label="审核意见内容"
                prop="auditAdvice"
                min-width='150'
                >
              </el-table-column>
              <el-table-column
                label="汇总意见"
                prop="isPassed"
                >
              </el-table-column>
            </el-table>
            <div slot="footer" class="dialog-footer" >
              <el-button type="primary" @click="showHasSelectedExpert = false">关 闭</el-button>
            </div>
        </el-dialog>
      </div>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import { mapGetters } from 'vuex'
import itemDialog from '@/views/project/item.vue'
import SelectTree from "@/components/treeSelect.vue";
import { getBatchNameList,getItemList,expertsQueryTitle,
getChildrenItem,getBatchGroupChild,getExpertsInfoWithAudit,queryFilterParam} from '@/api/data'
import querys from '@/components/querys'
import ItemAddCom from "@/views/project/itemAddCom"


export default {
  name: 'questions',
  data() {
  	return {
      showHasSelectedExpert:false,
      hasSelectedExpertInfoShow:[],
      tableDataLoding:false,
      colTimeList:[],
      isShowQuery:false,
      showQuery:false,
      tableHeightTab:"",
      timer:"",
			showAddDialog:false,   //弹窗是否展示-新增 修改 浏览
			showData:{
				itemId:null, //项目id
				specClass: "",//专项类型
				specClassName:"",//专项类型名称
				oldPath:"",//原来路由
				tableLabelArr:"",//表头
				phash:"1",//阶段1=立项 2=可研 3=储备 0=浏览
				parentItemId:null //父项id-新增下级时用
			},
			showDrawer:false,
      createOrg:"",
      //查询条件
      param:{
        "pageIndex": 1,
        "pageSize": 20,
        "itemName":"",
        "speciality":"",
        "createOrg":"",
        "itemPhaseDet":"",
        "batchName":"",
        "joinStatus":""
      },
      tableHeight:'',
      totalNum:0,
      currentPage:1,
      currentNum:20,
      tableData: [],
      tableLabel:[],
      tableShowData:[],
      //数据字典
      dictMap: new Map,
       //查询条件
      selectParamDict: new Map(),
      //字段类型字典
      columnTypeMap: new Map,
      //当前项目id
      tempItemId:"",
      token:'',
      valueId:"",
      deptArr:[],
      statusList:[
        {
          id:"16",
          name:"全部参加"
        },
        {
          id:"15",
          name:"部分参加"
        },
        {
          id:"14",
          name:"确认中"
        },
        {
          id:"4",
          name:"未选择"
        }
      ]
  	}
  },
  components: {
     querys,
     SelectTree,
     ItemAddCom
  },
  created(){
    this.tableHeight=window.innerHeight-190;
    
  },
  watch:{
		createOrg(){
      this.param.createOrg = this.createOrg
      this.currentPage = 1
			this.getTable(1,this.currentNum)
		}
	},
  mounted(){
    this.getItemLabelSearch()
    this.getTable(1,20)
    this.userId = sessionStorage.getItem('userId');
    this.token = sessionStorage.getItem('token');
    // this.getTreeDeptCode()
  },
  methods: {
    getValue(value){
			this.createOrg = value
    },
   
    // 懒加载table树形子数据
    tableLoad(row, treeNode, resolve) {
      console.log(row.batchGroupId)
      if(row.batchGroupId != "" && row.batchGroupId != undefined){
        let _params = {
          'batchGroupId':row.batchGroupId
        }
        getBatchGroupChild(_params).then(res => {
          if(res.data.successful){
            let childrenTable = res.data.resultValue

            let childrenData = this.getShowDateTable(childrenTable, this.dictMap, this.columnTypeMap)
            childrenData.forEach(function(item){
              item.isEdit = false
            })
            resolve(childrenData)
          }
        })
      }else{
        if(!row.children || row.children.length ==0){
          let params = {
            'parentItemId':row.itemId
          }
          getChildrenItem(params).then(res => {
            if(res.data.successful){
              let childrenTable = res.data.resultValue
              let childrenData = this.getShowDateTable(childrenTable, this.dictMap, this.columnTypeMap)
              childrenData.forEach(function(item){
                item.isEdit = false
              })
              this.$forceUpdate()
              resolve(childrenData)
            }
          })
        }else{
          console.log(row.children)
          let childrenData = this.getShowDateTable(row.children, this.dictMap, this.columnTypeMap)
          childrenData.forEach(function(item){
            item.isEdit = false
          })
          resolve(childrenData)
          // row.children = row.children
        }
        
      }
      
    },
    //展示已选专家列表
    showHasSelectedExpertDialog(data){
        let _this = this 
        _this.showHasSelectedExpert = true
        let isBatchPackage = false
        if(data.isBatchPackage ==1){
          isBatchPackage = true
        }else{
          isBatchPackage = false
        }
        getExpertsInfoWithAudit({"itemId":data.itemId,'isBatchPackage':isBatchPackage}).then(response=>{
            if(response.data.successful){
                console.log(response)
                _this.hasSelectedExpertInfoShow = response.data.resultValue
            }
        })
    },
    //获取查询条件下拉框
		getItemLabelSearch() {
			let _this = this
			queryFilterParam().then(response => {
				console.log('response', response)//后端返回数据
				if (response.data.successful) {
					let labelList = response.data.resultValue
          // 专业类别
          _this.selectParamDict.set('speciality', labelList.specialityList)
          _this.$forceUpdate()
				}
			})
		},
		//获取单位树形结构
		getTreeDeptCode(){
			let _this = this
			getUserDept().then(response=>{
				let valueId = response.data.resultValue
				getTreeDeptCode().then(response=>{
					console.log(response)
					if(response.data.successful){
						_this.valueId = valueId
						_this.deptArr = response.data.resultValue
					}
				})
			})
    },
    // 搜索框变化重新渲染表格
    selectItem(){
      this.currentPage = 1;
      this.getTable(1,this.currentNum)
    },
    
    // 子组件向父组件传递参数
    changeStatus(status) {
			this.showDrawer = status
			console.log('status'+status)
		},
    //展示项目信息详情
    showItemInfo(data){
      
      this.showData.itemId = data.itemId
			this.showData.phash = 0
			this.showDrawer = true;
			this.timer = new Date().getTime()
    },
     // 页面主表格获取当前每页条数
     handleSizeChange(val) {
      this.currentNum = val;
      this.getTable(this.currentPage,val)
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
      this.currentPage = val;
      this.getTable(val,this.currentNum)
    },
    getTable(page,size){
      let _this = this
      _this.tableDataLoding = true
      _this.param.index = page
      _this.param.pageSize = size
      _this.param.itemPhaseDet = "10"
      expertsQueryTitle().then(response => {
        if(response.data.successful){
            _this.totalNum = response.data.resultValue.totalNum
            _this.tableLabel = response.data.resultValue
            let arr = response.data.resultValue
            _this.tableLabel = _this.getLabelList(arr)
            _this.dictMap = _this.getDictArr(arr)
            _this.columnTypeMap = _this.getColumnTypeArr(arr)
            getItemList(_this.param).then(response => {//{}写需要的参数
            console.log('response',response)//后端返回数据
            if(response.data.successful){
                _this.totalNum = response.data.resultValue.totalNum
                _this.tableData = response.data.resultValue.tableData
                _this.tableShowData = _this.getShowDateTable(_this.tableData,_this.dictMap,_this.columnTypeMap)

            }
            _this.tableDataLoding = false
            
          }).catch(error => {
            console.log(error)//error
            _this.tableDataLoding = false
          })
        }
      })
      
        
    },
    //获取展示数据
    getShowDateTable(tableData,dictMap,columnTypeMap){
      let _this = this
      let arr = [];
      for(let i=0;i<tableData.length;i++){
        let tempObj = tableData[i];
        //拷贝对象
        let copyObj = new Object();
        //遍历获取的数据中的属性，如果属性名有和数据字典中的key相对应的，取出数据字典对应的值赋值过去
        for (let [key, value] of Object.entries(tempObj)) {
          console.log(key + ':' + value);
          if(key=="itemPhaseDet"){
               copyObj["itemPhaseDetT"] = value;
          }
          if(key=="speciality"){
              copyObj["specialityT"] = value;
          }
          if(dictMap.has(key)){
            let dictValueList = dictMap.get(key);
            for(let j=0;j<dictValueList.length;j++){
              let tempObj2 = dictValueList[j];
              if(tempObj2.codeValue==value){
                copyObj[key] = tempObj2.codeName;
              }
            }
          }else if(columnTypeMap.get(key)=="date" && value){
               copyObj[key+"Rel"] = value
               let timeStr = _this.formartDate(value)
               copyObj[key] = timeStr;
          }else{
            copyObj[key] = value;
          }
        }
        arr.push(copyObj);
      }
      return arr;
    },
    //获取字典
    getDictArr(labelList){ 
      let map = new Map();
      for(let i=0;i<labelList.length;i++){
        let temp = labelList[i];
        if(temp.codeList!=null){
          map.set(temp.colDname,temp.codeList);
        }
      }
      return map
    },
     //获取字段类型字典
    getColumnTypeArr(labelList){
      let map = new Map();
      for(let i=0;i<labelList.length;i++){
        let temp = labelList[i];
        map.set(temp.colDname,temp.colType);
      }
      return map
    },
    //获取插入和修改的表头
    getLabelList(labelList){ 
      let _this =this
      labelList.forEach(function (item) {
        _this.$set(item,'value','');
      })
      let common_table_info = {};//数组转对象，避免类型报错
      for(let i in labelList){
        common_table_info[i] = labelList[i];
      }
      return common_table_info
    },
     //时间戳字符串的转化
    formartDate(param) {
      var time = new Date(param);
      var y = time.getFullYear();
      var m = time.getMonth()+1;
      var d = time.getDate();
      return y+'-'+this.add0(m)+'-'+this.add0(d);
    },
    add0(m){
      return m<10?'0'+m:m;
    }
  }
}
</script>
<style scoped lang="stylus">
  
  .select_table td{
    white-space:nowrap;
  }
  .select_table td:not(:last-child){
   padding-right:10px;
  }
  .select_table{
    border-bottom:solid 1px #d4d4d4;
    margin-bottom:10px;
    padding-bottom:10px;
    margin-top:10px
  }
  
  .el-table .cell .color{
    color:#5892e5;
    cursor: pointer;
  }
  .dialog_tit{
    margin-bottom:10px;
  }
  .dialog_table{
    margin-top:10px;
    margin-bottom:10px;
